



























module.exports =
  name: 'introText'
  components:
    Typed: require './Typed'

  data: ->
    sentenceIndex: 0
    windowCount: 0

  methods:
    lineDone: ->
      @sentenceIndex += 1
    emitDone: ->
      @$emit('done')

  computed:
    taskNumber: -> return Math.floor((Math.random() * 10000) + 1)




